// 
var httpError = (code) => {
    switch(code) {
    case 500:
        return "Compiler fail";
    case 501:
        return "generate Java fail";
    case 502:
        return "server fail";
    case 510:
        return "";
    case 511:
        return "proto";
    case 512:
        return "proto:";  
    case 513:
        return "proto:";
    case 514:
        return "<packed>"; 
    case 515:
        return ":proto2<default><packed>;proto3<packed>;<packed>repeated primitive fields";
    case 516:
        return ":proto2<required>,<optional><repeated>,;proto3<singular><repeated>,<singular>";
    case 517:
        return "pb";
    case 518:
        return "protowire types:<type 0>,<type 1>,<type 2>,<type 5>;<type 3><type 4>";
    case 519:
        return "pb,";
    case 520:
        return "proto2,required";
    case 521:
        return "";
    case 522:
        return "";
    default:
        return ""+code;
    }
}
  
  
export default {
    httpError
}
  
  


